import { Quest, GameState } from './types';
import { ECONOMY, FAITH, SCIENCE } from './app/constants';

export const QUESTS: Record<string, Quest> = {
  first_steps: {
    id: 'first_steps',
    name: 'Первые шаги',
    description: 'Накопите 10 ресурсов',
    progress: 0,
    maxProgress: 10,
    isCompleted: false,
    isVisible: true,
    checkProgress: (state: GameState) => {
      // Прогресс основан на общем количестве ресурсов, но ограничен 10
      return Math.min(state.total, 10);
    },
    checkVisibility: (state: GameState) => {
      // Квест видим сразу после выбора страны
      return !!state.selectedCountry && state.stage >= 1;
    },
    checkCompletion: (state: GameState) => {
      // Квест завершен, если куплен любой апгрейд
      return state.total >= 10;
    }
  },
  first_upgrade: {
    id: 'first_upgrade',
    name: 'Первое улучшение',
    description: 'Приобретите улучшение',
    progress: 0,
    maxProgress: 1,
    isCompleted: false,
    isVisible: false,
    checkProgress: (state: GameState) => {
      return state.upgrades.some(upgrade => upgrade.level > 0) ? 1 : 0;
    },
    checkVisibility: (state: GameState) => {
      return state.total >= 10;
    },
    checkCompletion: (state: GameState) => {
      // Квест завершен, если куплен любой апгрейд
      return state.upgrades.some(upgrade => upgrade.level > 0)
    }
  },


  expand_borders: {
    id: 'expand_borders',
    name: 'Расширение границ',
    description: 'Захватите первую страну',
    progress: 0,
    maxProgress: 1,
    isCompleted: false,
    isVisible: false,
    checkProgress: (state: GameState) => {
      // Прогресс 0 если захвачена только выбранная страна, 1 если больше
      return state.conqueredCountries.length > 1 ? 1 : 0;
    },
    checkVisibility: (state: GameState) => {
      // Квест становится видимым когда доступна хотя бы одна страна для захвата
      // Это происходит когда есть события на карте или достаточно ресурсов
      return state.mapEvents.length > 0 || state.total > 500;
    },
    checkCompletion: (state: GameState) => {
      // Квест завершен, если захвачено больше одной страны
      return state.conqueredCountries.length > 1;
    }
  },
};

// Функция для получения всех квестов в виде массива
export const getAllQuests = (): Quest[] => {
  return Object.values(QUESTS);
};

// Функция для получения квеста по ID
export const getQuestById = (id: string): Quest | undefined => {
  return QUESTS[id];
}; 