// Генерирует случайную позицию на экране
function getRandomPosition(windowSize: { width: number, height: number }): [number, number] {
  // Ограничиваем область появления событий, чтобы они не появлялись слишком близко к краям
  const margin = 50;
  const x = margin + Math.random() * (windowSize.width - margin * 2);
  const y = margin + Math.random() * (windowSize.height - margin * 2);
  return [x, y];
}

// Проверяет, может ли событие быть активировано
function canTriggerEvent(event: string, state: GameState): boolean {
  // Проверяем, не активировано ли уже это событие
  const isAlreadyActive = state.activeEvents.some(e => e.id === event);
  if (isAlreadyActive) return false;
  const eventData = GAME_EVENTS[event];
  const eventHistory = state.eventHistory[event];

  // Проверяем историю события
  if (eventData.once && eventHistory?.count > 0) return false;
  // Если событие имеет кулдаун и не прошло достаточно времени
  if (eventHistory && eventData.cooldown && (Date.now() - (eventHistory.lastTriggered || 0)) < eventData.cooldown) {
    return false;
  }
  // Проверяем все условия события
  return eventData.conditions.every(condition => condition(state));
}

// Выбирает событие для активации
function selectEvent(state: GameState): GameEvent | null {
  const candidateEvents: GameEvent[] = [];
  
  // Перебираем все события
  for (const event of state.events) {
    if (canTriggerEvent(event, state)) {
      candidateEvents.push(GAME_EVENTS[event]);
    }
  }
  
  // Теперь у нас есть список конкретных событий, которые могут быть активированы
  if (candidateEvents.length === 0) return null;
  
  // Выбираем событие с наибольшим весом
  const maxWeight = Math.max(...candidateEvents.map(event => event.weight));
  const maxWeightEvents = candidateEvents.filter(event => event.weight === maxWeight);
  
  // Если нашли события с максимальным весом, выбираем случайное из них
  if (maxWeightEvents.length > 0) {
    const randomIndex = Math.floor(Math.random() * maxWeightEvents.length);
    const selectedEvent = maxWeightEvents[randomIndex];
    
    // Проверяем удачу - вероятность активации события равна его весу (в процентах)
    if (Math.random() * 100 < selectedEvent.weight) {
      return selectedEvent;
    }
  }
  
  // Если не удалось выбрать событие или не повезло, возвращаем null
  return null;
}

// Предрасчет условий видимости и доступности для талантов
function recalculateTalents(state: GameState): StateTalent[] {
  // Получаем список активных талантов
  const activeTalentIds = state.talents.filter(t => t.state === 'active').map(t => t.id);
  
  // Обрабатываем таланты и добавляем вычисленную информацию
  return TALENTS.map(talent => {
    const stateTalent = state.talents.find(t => t.id === talent.id);
    // Проверяем условие видимости
    const isVisible = talent.visible.every(condition => condition(state));
    
    // Проверяем требования для разблокировки таланта
    const requirementsMet = talent.requires.every(req => 
      !req.talentId || activeTalentIds.includes(req.talentId)
    );
    // достаточно ли у игрока денег
    const hasEnough = state.talentPoints >= talent.cost;
    // достаточно ли потрачено
    const spentEnough = [ECONOMY, SCIENCE, FAITH].includes(talent.category as any) ? state.spheres[talent.category].talentPointsSpent >= (talent.position[1] -5) : true;
    
    // Проверяем условия доступности
    const isAvailable = requirementsMet && talent.available.every(condition => condition(state)) && hasEnough && spentEnough;

    // Проверяем условия для затенения
    const isShaded = talent.shade?.some(condition => condition(state));

    // Возвращаем обновленный талант со всеми вычисленными свойствами
    return {
      id: talent.id,
      isVisible,
      isAvailable,
      isShaded,
      state: stateTalent!.state
    };
  });
}

// Предрасчет видимости апгрейдов
function recalculateUpgrades(state: GameState): StateUpgrade[] {
  return UPGRADES.map(upgrade => {
    // Получаем текущий уровень и стоимость из состояния
    const stateUpgrade = state.upgrades.find(u => u.id === upgrade.id);
    const level = stateUpgrade ? stateUpgrade.level : 0;
    const cost = stateUpgrade ? stateUpgrade.cost : upgrade.cost;
    const CPS = stateUpgrade ? stateUpgrade.CPS : upgrade.CPS;
    const available = stateUpgrade ? stateUpgrade.available : upgrade.available;

    // Проверяем условие видимости
    let isVisible: boolean;
    if (typeof upgrade.visible === 'boolean') {
      isVisible = upgrade.visible;
    } else {
      // Если visible - это функция, вызываем её с текущим состоянием
      isVisible = upgrade.visible(state);
    }
    
    return {
      id: upgrade.id,
      isVisible: state.upgrades.find(u => u.id === upgrade.id)?.isVisible || isVisible,
      level,
      cost,
      CPS,
      available,
    };
  });
}

// Предрасчет состояния квестов
function recalculateQuests(state: GameState): any[] {
  return state.quests.map(questState => {
    const questDefinition = QUESTS[questState.id];
    if (!questDefinition) return questState;
    
    // Создаем обновленное состояние квеста
    const updatedQuest = { ...questState };
    
    // Обновляем видимость
    updatedQuest.isVisible = questDefinition.checkVisibility(state);
    
    // Если квест уже завершен, не обновляем прогресс
    if (!questState.isCompleted) {
      // Обновляем прогресс
      updatedQuest.progress = questDefinition.checkProgress(state);
      
      // Проверяем завершение
      if (questDefinition.checkCompletion(state)) {
        updatedQuest.isCompleted = true;
        updatedQuest.completedAt = Date.now();
      }
    }
    
    return updatedQuest;
  });
}

// Проверяет события и создает новое активное событие, если возможно
function checkEvents(state: GameState, windowSize: { width: number, height: number }): {id: string; position: number[]}[] {
  // Выбираем событие для активации
  const eventToTrigger = selectEvent(state);
  
  // Создаем копию текущих активных событий
  const updatedActiveEvents = [...state.activeEvents];
  
  // Если есть новое событие для добавления
  if (eventToTrigger) {
    state.eventHistory[eventToTrigger.id] = {
      id: eventToTrigger.id,
      lastTriggered: Date.now(),
      count: (state.eventHistory[eventToTrigger.id]?.count || 0) + 1
    };
    
    // Определяем позицию для нового события
    let position: number[];
    if (eventToTrigger.position === "random") {
      position = getRandomPosition(windowSize);
    } else if (Array.isArray(eventToTrigger.position)) {
      position = eventToTrigger.position;
    } else {
      position = getRandomPosition(windowSize);
    }
    
    updatedActiveEvents.push({
      id: eventToTrigger.id,
      position
    });
  }
  
  return updatedActiveEvents;
}

// Объединенная функция для обработки всех типов завоеваний
const checkConquests = (state: GameState, deltaTime: number) => {
  const updates = {
    lastConquest: state.lastConquest,
    conqueredCountries: [...state.conqueredCountries],
    empirePerimeter: [...state.empirePerimeter],
    activeConquests: [...state.activeConquests],
    resources: state.resources,
    flags: [...state.flags],
    autoClickPower: state.autoClickPower
  };

  const now = Date.now();

  // 1. Проверяем автоматическое завоевание соседних стран (моментальное)
  const countryToConquer = state.empirePerimeter[Math.round(Math.random() * state.empirePerimeter.length)];
  const possibleToConquer = Math.random() > (COUNTRIES[countryToConquer]?.conquestChanse || 0);
  // no conquest till we have 10 clicks
  if (countryToConquer && possibleToConquer && state.total > 10 &&
      !updates.conqueredCountries.includes(countryToConquer) &&
      (now - state.lastConquest.lastTriggered) >= (CONQUEST_CD / state.conquestSpeed)) {

    // Обновляем периметр империи
    if(COUNTRIES[countryToConquer]){
        const conqueredCountryPerimeter = COUNTRIES[countryToConquer]?.neighbours.filter(c => 
        !updates.empirePerimeter.includes(c)
      );
      updates.empirePerimeter = [
        ...updates.empirePerimeter.filter(c => c !== countryToConquer),
        ...conqueredCountryPerimeter
      ];
    }

    try{
      // add auto click bonus
      updates.autoClickPower += COUNTRIES[countryToConquer]?.autoPower ?? 0;
    }catch(e){
      console.log(countryToConquer, ' error', e)
    }

    

    // Добавляем завоеванную страну
    updates.conqueredCountries.push(countryToConquer);
    updates.lastConquest = {
      lastTriggered: now,
      country: countryToConquer
    };
  }

  // 2. Проверяем завоевания через флаги (с задержкой)
  updates.activeConquests = updates.activeConquests.filter((conquest) => {
    const elapsed = now - conquest.startTime;
    
    if (elapsed >= conquest.duration) {
      // Захват через флаг завершен
      const countryData = COUNTRIES[conquest.countryId];
      
      // Захватываем страну (проверяем что еще не захвачена)
      if (!updates.conqueredCountries.includes(conquest.countryId)) {
        updates.conqueredCountries.push(conquest.countryId);
        
        // Добавляем силу автоматического клика
        updates.autoClickPower += countryData?.autoPower ?? 0;

        //расширяем периметр
        updates.empirePerimeter = [
          ...state.empirePerimeter.filter(c => c !== conquest.countryId),//filter out that contry for caution
          ...countryData.neighbours.filter(c => !state.empirePerimeter.includes(c)) // perimeter around new country, but filter overlaps
        ]

        
        // Выполняем бонус за захват, если есть
        if (countryData?.conquestBonus) {
          // Создаем временное состояние для применения бонуса
          const tempState = { 
            ...state, 
            conqueredCountries: updates.conqueredCountries,
            resources: updates.resources 
          };
          countryData.conquestBonus(tempState);
          // Применяем изменения обратно к updates
          updates.resources = tempState.resources;
        }
      }
      
      // Удаляем использованный флаг
      updates.flags = updates.flags.filter((flag) => flag.id !== conquest.flagId);
      
      return false; // Удаляем завершенный захват
    }
    
    return true; // Оставляем активный захват
  });

  return updates;
}
  
// Проверяет, можно ли активировать проблему
function canTriggerProblem(problem: stateProblem, state: GameState): boolean {
  // Проверяем, не активирована ли уже эта проблема
  const activeVersion = state.activeProblems.find(p => p.id === problem.id);
  if (!!activeVersion) return false;
  
  return true;
}

// Проверяет проблемы проектов и активирует их при необходимости
function checkProblems(state: GameState, windowSize: { width: number, height: number }): ActiveProblem[] {
  const updatedActiveProblems = [...state.activeProblems];
  
  // Перебираем все активные проекты
  state.activeProjects.forEach(project => {
    const projectData = PROJECTS[project.id];
    
    // Если у проекта есть проблемы и он не завершен, проверяем их
    if (projectData.problems && project.progress < 100) {
      // Выполняем все функции проблем, которые могут добавить новые проблемы в state.problems
      projectData.problems.forEach(problemEffect => problemEffect(state));
    }
  });

  // Проверяем вероятность активации для каждой проблемы из списка
  state.problems.forEach(problem => {
    const problemData = PROBLEMS[problem.id];
    
    if (problemData && canTriggerProblem(problem, state)) {
      // Проверяем шанс на активацию (вес проблемы от 0 до 100%)
      if (Math.random() * 100 < problemData.weight) {
        // Определяем позицию для новой проблемы
        let position: number[];
        if (problemData.position === "random") {
          position = getRandomPosition(windowSize);
        } else {
          position = problemData.position;
        }
        // Добавляем новую проблему
        updatedActiveProblems.push({
          id: problem.id,
          projectId: problem.projectId,
          startTime: Date.now(),
          position,
        });
        
        // Удаляем проблему из списка доступных, чтобы она не активировалась повторно
        state.problems = state.problems.filter(p => p.id !== problem.id);
      }
    }
  });
  
  return updatedActiveProblems;
}

// Функция для обновления списка новостей
function getNewsForState(state: GameState): string[] {
  // Восстанавливаем все новости для текущего и предыдущих этапов
  return Object.keys(NEWS).filter(id => {
    const news = NEWS[id];
    return news.stage === state.stage;
  });
}

// Функция для выбора случайной новости, соответствующей текущему этапу игры
function selectNews(state: GameState): [string | null, string[]] {
  // Если нет новостей вообще, восстанавливаем список
  if (state.newsQueue.length === 0) {
    // В текущем тике не показываем новость
    return ['', getNewsForState(state)];
  }

  // Берем первую новость из очереди и удаляем ее
  const newsId = state.newsQueue[0];
  const newsQueue = state.newsQueue.slice(1);
  return [newsId, newsQueue];

}

// Функция для проверки необходимости отображения новостей
function checkNews(state: GameState): [string | null, string[], number] {
  const now = Date.now();
  let activeNews = state.activeNews;
  let newsQueue = state.newsQueue;
  let lastNewsTime = state.lastNewsTime;
  // Проверяем, прошло ли достаточно времени с момента последнего показа новости
  if (now - state.lastNewsTime > NEWS_INTERVAL) {
    lastNewsTime = now;
    [activeNews, newsQueue] = selectNews(state);
  }
  
  // Выбираем новость для отображения
  return [activeNews, newsQueue, lastNewsTime]  ;
}

// Проверяет и обновляет генерацию талантов для стран
function checkBeacons(state: GameState, windowSize: { width: number, height: number }) {
  const updates = {
    countryTalentState: { ...state.countryTalentState },
    talentBeacons: [...state.talentBeacons]
  };

  const now = Date.now();
  
  // Проверяем все захваченные страны (кроме выбранной игроком)
  const eligibleCountries = state.conqueredCountries.filter(country => 
    country !== state.selectedCountry && COUNTRIES[country]
  );

  eligibleCountries.forEach(countryName => {
    const countryData = COUNTRIES[countryName];
    if (!countryData) return;

    // Инициализируем состояние страны если его нет
    if (!updates.countryTalentState[countryName]) {
      updates.countryTalentState[countryName] = {
        talentsGenerated: 0,
        maxTalents: countryData.maxTalents,
        lastTalentGenerated: 0,
        talentCD: countryData.talentCD
      };
    }

    const countryState = updates.countryTalentState[countryName];

    // Проверяем условия для генерации пуансона:
    // 1. У страны еще есть ресурс талантов
    // 2. Прошел кулдаун
    // 3. На карте не больше 4 пуансонов
    // 4. На этой стране нет не кликнутого пуансона
    // 5. global cd прошел
    const hasRemainingTalents = countryState.talentsGenerated < countryState.maxTalents;
    const cooldownPassed = (now - countryState.lastTalentGenerated) >= countryState.talentCD;
    const canCreateBeacon = updates.talentBeacons.length < 4;
    const hasActiveBeacon = updates.talentBeacons.some(beacon => beacon.countryId === countryName);
    const globalCooldownPassed = (now - countryState.lastTalentGenerated) >= BEACON_GLOBAL_CD;


    if (hasRemainingTalents && cooldownPassed && canCreateBeacon && !hasActiveBeacon && globalCooldownPassed) {
      // Создаем пуансон на случайной позиции на экране
      const [x, y] = getRandomPosition(windowSize);
      
      const newBeacon = {
        id: `talent-beacon-${countryName}`,
        countryId: countryName,
        x,
        y,
        createdAt: now
      };

      updates.talentBeacons.push(newBeacon);
      
      // Обновляем состояние страны
      countryState.talentsGenerated += 1;
      countryState.lastTalentGenerated = now;
    }
  });

  return updates;
}



export const calculateTick = (state: GameState, deltaTime: number, windowSize: { width: number, height: number }): Partial<GameState> => {
  const updates: Partial<GameState> = {};
  updates.resources = state.resources;
  
  // Останавливаем все вычисления при поражении
  if (state.gameOver) {
    return updates;
  }
  
  if (state.stage === 0) {
    return updates;
  }

  // Проверяем таймер смерти (только для первого этапа)
  if (state.stage === 1 && state.deathTimer.isActive) {
    const elapsed = Date.now() - state.deathTimer.startTime;
    
    // Если время истекло, игра проиграна
    if (elapsed >= state.deathTimer.totalDuration) {
      updates.gameOver = true;
      updates.gameOverReason = "Время истекло! Второй удар астероида уничтожил человечество.";
      updates.deathTimer = {
        ...state.deathTimer,
        isActive: false
      };
    }
  }

  // Check for stage transition conditions
  if (state.stage === 1) {
    // Check if all countries are conquered
    const allCountriesConquered = state.conqueredCountries.length >= COUNTRY_NAMES.length - 10;


    const shouldTriggerTransition = allCountriesConquered

    // Если условия выполнены, но срочное сообщение еще не активно
    if (shouldTriggerTransition && !state.urgentMessage.isActive) {
      updates.urgentMessage = {
        isActive: true,
        startTime: Date.now(),
        duration: 45000 // 45 секунд
      };
    }

    // Если срочное сообщение активно, проверяем истечение времени
    if (state.urgentMessage.isActive) {
      const elapsed = Date.now() - state.urgentMessage.startTime;
      
      // Если время истекло, переходим на второй этап
      if (elapsed >= state.urgentMessage.duration) {
        updates.stage = 2;
        updates.secondStage = [...state.secondStage, 'space_travel_unlocked'];
        updates.urgentMessage = {
          isActive: false,
          startTime: 0,
          duration: 0
        };
        // Останавливаем таймер смерти при переходе на второй этап
        updates.deathTimer = {
          ...state.deathTimer,
          isActive: false
        };
      }
    }
  }

  

  
  

  // Предрасчитываем условия видимости и доступности для талантов
  updates.talents = recalculateTalents(state);
  
  // Предрасчитываем видимость апгрейдов
  updates.upgrades = recalculateUpgrades(state);
  
  // Обновляем квесты
  updates.quests = recalculateQuests(state);

  // Обновляем события и удаляем устаревшие
  let newActiveEvents = checkEvents(state, windowSize);
  
  // Проверяем какие события должны быть автоматически удалены по времени
  newActiveEvents = newActiveEvents.filter(eventObj => {
    const eventHistory = state.eventHistory[eventObj.id];
    if (!eventHistory) return true;
    
    const eventAge = Date.now() - eventHistory.lastTriggered;
    // Если событие существует дольше EVENT_AUTO_CLOSE_TIME, удаляем его и применяем эффекты
    if (eventAge >= EVENT_AUTO_CLOSE_TIME) {
      // Удаляем из списка доступных событий, если оно одноразовое
      if (GAME_EVENTS[eventObj.id].once) {
        state.events = state.events.filter(e => e !== eventObj.id);
      }
      
      return false;
    }
    return true;
  });
  
  updates.activeEvents = newActiveEvents;
  
  // Проверяем и обновляем проблемы
  updates.activeProblems = checkProblems(state, windowSize);
  
  // Проверяем и обновляем новости
  [updates.activeNews, updates.newsQueue, updates.lastNewsTime] = checkNews(state);

  // Проверяем и обновляем генерацию талантов (только для первого этапа)
  if (state.stage === 1) {
    const talentUpdates = checkBeacons(state, windowSize);
    updates.countryTalentState = talentUpdates.countryTalentState;
    updates.talentBeacons = talentUpdates.talentBeacons;
  }

  
  updates.activeProjects = state.activeProjects;
  
  // Update project progress for active projects
  updates.activeProjects.forEach(project => {
    const projectData = PROJECTS[project.id];
    if (project.status === 'in_progress') {
      const progressIncrement = projectData.speed * state.projectSpeed;
      project.progress = Math.min(100, project.progress + progressIncrement);

      if (project.progress >= 100) {
        // Просто помечаем как успешный, но не применяем эффекты
        // Эффекты будут применены в редьюсере completeProject
        project.status = 'success';
      }
    }
  });

  // Обработка повреждений корабля (только для космического этапа)
  if (state.stage === 2 ) { // TODO: добавить проверку на нужный тип корабля, надо ли ему вообще лететь
    if (state.ship.flightStartTime  === 0) {
      // начинаем полет
      state.ship.flightStartTime = Date.now();
    }
    const flightTime = Date.now() - state.ship.flightStartTime;
    const updatedShip = { ...state.ship };
    
    // Рассчитываем базовый урон с учетом волн повреждений
    let damageMultiplier = 1;
    
    // Проверяем, находимся ли мы в одной из волн повреждений
    for (const wave of SHIP_DAMAGE_WAVES) {
      if (flightTime >= wave.startTime && flightTime <= wave.startTime + wave.duration) {
        damageMultiplier += wave.multiplier;
      }
    }
    
    // Добавляем синусоидальную составляющую для естественных колебаний урона
    const oscillationFactor = 1 + Math.sin(flightTime / 10000) * 0.3;
    
    // Рассчитываем итоговый урон с учетом прошедшего времени
    const hullDamage = SHIP_BASE_DAMAGE_RATE * damageMultiplier * oscillationFactor * (deltaTime / 1000);
    const shieldDamage = SHIP_BASE_SHIELD_DAMAGE_RATE * damageMultiplier * oscillationFactor * (deltaTime / 1000);

    // Получаем список активных щитов
    const activeShields = Object.entries(updatedShip.activeShields)
                                .filter(([_, active]) => active)
                                .map(([sphere]) => sphere);
    
    
      // Если щиты выключены, весь урон идет корпусу
      updates.resources -= hullDamage;
  
    
    
    
    // Проверяем целостность корпуса
    if (updates.resources <= 0) {
      // Если целостность корпуса упала до нуля, игра проиграна
      updates.gameOver = true;
      updates.gameOverReason = "Корабль был уничтожен во время космического полета!";
    }
    
    updates.ship = updatedShip;
  }

  // Проверяем и обновляем все типы завоеваний (только для первого этапа)
  if (state.stage === 1) {
    const conquestUpdates = checkConquests(state, deltaTime);
    Object.assign(updates, conquestUpdates);
  }
  // Update resources based on CPS from all upgrades with multipliers
  // no auto clicks, till we have 10 manual
  let totalAutoIncome = 0;
  
  if (state.total > 10) {
    // Рассчитываем доход от каждого апгрейда с учетом его множителей
    state.upgrades.forEach(stateUpgrade => {
      const originalUpgrade = UPGRADES.find(u => u.id === stateUpgrade.id);
      if (originalUpgrade && stateUpgrade.level > 0) {
        // Для COMMON апгрейдов добавляем к общему автоматическому доходу
        if (originalUpgrade.sphere === COMMON) {
          totalAutoIncome += calculateTotalUpgradeCPS(
            originalUpgrade.CPS,
            stateUpgrade.level,
            originalUpgrade.id,
            originalUpgrade.sphere,
            state
          );
        }
      }
    });
  }
  
  updates.resources = state.resources + totalAutoIncome;
  // Update total, calculated for progression
  updates.total = state.total + totalAutoIncome;
  
  return updates;
};

// Web Worker
/* eslint-disable-next-line no-restricted-globals */
self.onmessage = (e: MessageEvent) => {
  switch (e.data.type) {
    case 'TICK':
      const action = calculateTick(e.data.state, e.data.deltaTime, e.data.windowSize);
      postMessage(action);
      break;
    case 'STATE_UPDATED':
      // Просто запоминаем новое состояние и ничего не отправляем обратно
      // На следующем тике будут использованы актуальные данные
      break;
  }
};
